import React, { useState } from 'react';
import '../../style/avatarselection.css';

const SelectionPage = () => {
  const [selectedOption, setSelectedOption] = useState(null);
  const [isHovering, setIsHovering] = useState(null);

  // Replace these with your actual image URLs
  const options = [
  {
    id: 'option1',
    title: 'Galactic Hero',
    image: 'https://img.freepik.com/premium-vector/beautiful-professional-cartoon-character-design-vector-illustration_1253044-5822.jpg',
    description: 'Ready to conquer any challenge with cosmic power!'
  },
  {
    id: 'option2', 
    title: 'Super Star Girl',
    image: 'https://img.freepik.com/premium-vector/beautiful-girl-children-character-vector-illustration_1287274-43991.jpg?semt=ais_hybrid&w=740',
    description: 'Fearless and fierce  the ultimate champion of justice!'
  }
];


  const handleSelection = (optionId) => {
    setSelectedOption(optionId);
    console.log('Selected:', optionId);
    // Add your navigation logic here
  };

  const handleMouseEnter = (optionId) => {
    setIsHovering(optionId);
  };

  const handleMouseLeave = () => {
    setIsHovering(null);
  };

  return (
    <div className="selection-container">
      <div className="selection-header">
        <h1>Choose Your Dream Avatar</h1>
        {/* <p>Select one of the options below to continue</p> */}
      </div>
      
      <div className="options-container">
        {options.map((option) => (
          <div
            key={option.id}
            className={`option-circle ${selectedOption === option.id ? 'selected' : ''} ${isHovering === option.id ? 'hovering' : ''}`}
            onClick={() => handleSelection(option.id)}
            onMouseEnter={() => handleMouseEnter(option.id)}
            onMouseLeave={handleMouseLeave}
          >
            <div className="circle-3d">
              <div 
                className="circle-image"
                style={{ backgroundImage: `url(${option.image})` }}
              >
                <div className="circle-overlay">
                  <div className="circle-content">
                    <h3>{option.title}</h3>
                    <p>{option.description}</p>
                  </div>
                </div>
              </div>
            </div>
            
            {selectedOption === option.id && (
              <div className="selection-indicator">
                <div className="checkmark"></div>
              </div>
            )}
          </div>
        ))}
      </div>

      {selectedOption && (
        <div className="continue-section">
          <button className="continue-button">
            Continue
          </button>
        </div>
      )}
    </div>
  );
};

export default SelectionPage;