import React, { useState } from 'react';
import '../../style/signin.css';

const SignIn = () => {
  const [formData, setFormData] = useState({
    studentName: '',
    school: '',
    grade: ''
  });
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});

  const schools = [
    'Royal College',
    'Ananda College', 
    'Ladies College',
    'St. Thomas College'
  ];

  const grades = [
    'Grade 1',
    'Grade 2',
    'Grade 3',
    'Grade 4',
    'Grade 5',
    'Grade 6',
    'Grade 7'
  ];

  const handleInputChange = (field, value) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
    
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({
        ...prev,
        [field]: ''
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.studentName.trim()) {
      newErrors.studentName = 'Please enter student name';
    } else if (formData.studentName.trim().length < 2) {
      newErrors.studentName = 'Name must be at least 2 characters';
    }
    
    if (!formData.school) {
      newErrors.school = 'Please select a school';
    }
    
    if (!formData.grade) {
      newErrors.grade = 'Please select a grade';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }
    
    setLoading(true);
    try {
      console.log('Form values:', formData);
      // Add your sign in logic here
      alert('Sign in successful!');
    } catch (error) {
      alert('Sign in failed. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="main-frame">
      <div className="signin-card">
        <div className="signin-header">
          <h2>Student Sign In</h2>
        </div>
        
        <div className="signin-form">
          <div className="form-group">
            <label htmlFor="studentName">Student Name</label>
            <input
              id="studentName"
              type="text"
              value={formData.studentName}
              onChange={(e) => handleInputChange('studentName', e.target.value)}
              placeholder="Enter student name"
              className={errors.studentName ? 'error' : ''}
            />
            {errors.studentName && <span className="error-message">{errors.studentName}</span>}
          </div>

          <div className="form-group">
            <label htmlFor="school">School</label>
            <select
              id="school"
              value={formData.school}
              onChange={(e) => handleInputChange('school', e.target.value)}
              className={errors.school ? 'error' : ''}
            >
              <option value="">Select your school</option>
              {schools.map((school) => (
                <option key={school} value={school}>
                  {school}
                </option>
              ))}
            </select>
            {errors.school && <span className="error-message">{errors.school}</span>}
          </div>

          <div className="form-group">
            <label htmlFor="grade">Grade</label>
            <select
              id="grade"
              value={formData.grade}
              onChange={(e) => handleInputChange('grade', e.target.value)}
              className={errors.grade ? 'error' : ''}
            >
              <option value="">Select your grade</option>
              {grades.map((grade) => (
                <option key={grade} value={grade}>
                  {grade}
                </option>
              ))}
            </select>
            {errors.grade && <span className="error-message">{errors.grade}</span>}
          </div>

          <button 
            type="button"
            onClick={handleSubmit}
            disabled={loading}
            className="signin-button"
          >
            {loading ? 'Signing In...' : 'Sign In'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default SignIn;